.card {
  margin: 5px;
  &__card {
    margin: 10px;
    padding: 20px;
    border: 1px solid grey;
    border-radius: 5px;
    transition: transform 1s;
    background-color: #e9e9e9;

    &:hover {
      cursor: pointer;
      transform: scale(1.1);
    }
  }

  &__image-container {
    width: 210px;
  }

  &__image {
    width: 200px;
    height: 200px;
    padding-bottom: 10px;
  }

  &__name {
    padding: 5px 10px;
    text-align: center;
    text-transform: capitalize;
    font-size: 1.2em;
    font-weight: bold;
  }

  &__types {
    display: flex;
    justify-content: center;
  }

  &__type {
    margin: 0 5px;
    padding: 5px 10px;
    text-transform: capitalize;
    border-radius: 5px;
  }

  &__moves {
    text-align: center;
    font-size: 16px;
    margin-top: 10px;
  }

  &__height-container,
  &__weight-container {
    border-radius: 10px;
    width: 70px;
    text-align: center;
    font-size: 16px;
  }

  &__container-wrapper {
    display: flex;
    justify-content: space-between;
  }
}
